@use 'styles/colors';
@import 'styles/base';

:host {
  display: flex;
  width: 100%;
  align-items: center;
  gap: 10px;

  .leading, .trailing {
    flex: 1;
    height: 1px;
    background-color: #E8E8E8;
  }

  .text {
    @include proxima(16px, 600, colors.$foreground_dark);
    text-transform: uppercase;
  }
}
